/* src/App.css */
.App {
  text-align: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
  color: #e0e0e0;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  max-width: 800px;
  margin: 0 auto;
}

h1 {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  color: #fff;
  text-shadow: 0 2px 4px rgba(0,0,0,0.3);
  font-weight: 300;
}

.timer-settings {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 15px;
  padding: 2rem;
  margin: 2rem 0;
  width: 90%;
  max-width: 600px;
  backdrop-filter: blur(10px);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
}

.timing-options, .duration-options {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
  margin: 1.5rem 0;
}

.timing-option, .duration-option {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: rgba(255, 255, 255, 0.1);
  padding: 0.8rem 1.2rem;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.timing-option:hover, .duration-option:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
}

.timing-option input, .duration-option input {
  margin-right: 8px;
}

.control-button {
  padding: 1rem 3rem;
  font-size: 1.2rem;
  border: none;
  border-radius: 50px;
  background: linear-gradient(45deg, #4a90e2, #63b3ed);
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0,0,0,0.2);
}

.control-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0,0,0,0.3);
}

.control-button.running {
  background: linear-gradient(45deg, #e53e3e, #fc8181);
}

.volume-control {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  width: 80%;
  max-width: 300px;
  margin-top: 2rem;
  padding: 1.5rem;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  backdrop-filter: blur(10px);
}

.volume-control label {
  font-size: 1.1rem;
  color: #fff;
}

.volume-control input[type="range"] {
  width: 100%;
  height: 4px;
  -webkit-appearance: none;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 2px;
  cursor: pointer;
}

.volume-control input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 20px;
  height: 20px;
  background: #4a90e2;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s ease;
}

.volume-control input[type="range"]::-webkit-slider-thumb:hover {
  transform: scale(1.1);
  background: #63b3ed;
}

.state-display {
  margin: 1.5rem 0;
  padding: 1rem 2rem;
  background: rgba(255, 255, 255, 0.08);
  border-radius: 10px;
  font-size: 1.1rem;
  color: #fff;
  backdrop-filter: blur(5px);
}

h2, h3 {
  color: #fff;
  margin: 1.5rem 0;
  font-weight: 300;
  text-shadow: 0 2px 4px rgba(0,0,0,0.2);
}

.disabled {
  opacity: 0.5;
  cursor: not-allowed;
  pointer-events: none;
}